<template>
  <!-- 表单信息收集页面 -->
  <a-layout class="container">
    <!-- 搜索框 -->
    <a-input-search
      v-model:value="formTitle"
      placeholder="请输入表单名称"
      :loading = "loading"
      :enter-button="!loading"
      class="search-button"
      @search="(value, event) => search(value, event)"
    />

    <!-- 分页器 -->
    <a-layout>
      <a-pagination 
        v-model:current="current" 
        v-model:pageSize="pageSize"
        :total="formDataTotal"
        :showSizeChanger="false"
        :show-total="total => `总共有 ${total} 条记录`"
        show-less-items	
        show-quick-jumper
        class="pagination"
        @change="(page, pageSize) => pageChange(page, pageSize)"
      />
    </a-layout>

    <!-- 分割线 -->
    <a-divider style="margin-top: 0;"></a-divider>

    <!-- 表单信息集合 -->
    <a-layout>
      <a-table
        :dataSource="formDataGroup" 
        :columns="columns"
        :pagination="false"
        :loading="loading"  
        size="small"
        style="margin-left: 10px; margin-right: 10px;"
        bordered
      >
        <template #bodyCell="{ column, text, record }">
          <template v-if="column.dataIndex === 'operation'">     
            <a-button @click="() => previewShow(record.formData)" size="small">
              <template #icon>
                <eye-outlined />
              </template>
              预览
            </a-button>
            <a-button @click="() => openLink(record.url)" size="small">
              <template #icon>
                <link-outlined />
              </template>
              打开链接
            </a-button>
            <a-button @click="() => copyText(record.url)" size="small">
              <template #icon>
                <copy-outlined />
              </template>
              复制链接
            </a-button>
            <a-button @click="() => qrcodeCreate(record.url)" size="small">
              <template #icon>
                <qrcode-outlined />
              </template>
              二维码
            </a-button>
            <a-button @click="() => copyText(record.formData)" size="small">
              <template #icon>
                <copy-outlined />
              </template>
              复制 JSON
            </a-button>
          </template>
        </template>
        <template #title>已发布表单的统计信息</template>
      </a-table>
    </a-layout>
    
    <!-- 提示等 -->
    <!-- 二维码生成 -->
    <a-modal
      v-model:visible="qrcodeVisible" 
      title="二维码生成"
      okText="确定"
      cancelText=" "
      :destroyOnClose=true
      @ok="() => qrcodeVisible = false"
    >
      <a-typography-link :href="selectedUrl" target="_blank">
        链接：{{ selectedUrl }}
      </a-typography-link>
      <a-layout style="align-items: center;">
        <qrcode-vue 
          :value="selectedUrl" 
          :size="size" 
          id="qrcode" 
          style="border: 2px solid rgb(55, 55, 255);"
        />
        <a-button class="download-button" @click="() => qrcodeDownload('qrcode', 'qrcode.png')">下载</a-button>
      </a-layout>
    </a-modal>
    <!-- 表单预览框 -->
    <a-drawer
      :width="500"
      size="large"
      title="表单预览"
      placement="top"
      :visible="previewVisible"
      :destroyOnClose=true
      @close="previewClose"
    >
      <template #extra>
        <a-button type="primary" @click="previewClose">关闭预览</a-button>
      </template>
      <FormPreview :form-data-json="selectedJson" />
    </a-drawer>
  </a-layout>
</template>

<script setup>
import { onMounted, ref } from 'vue';
import axios from 'axios';
import QrcodeVue from 'qrcode.vue';
import FormPreview from '@/components/Form/FormPreview.vue';
import { qrcodeDownload } from '@/utils/qrcodeApi';
import { copyText } from '@/utils/copyText';
import { EyeOutlined, LinkOutlined, CopyOutlined, QrcodeOutlined } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';

// 记录总条数
const formDataTotal = ref(100);
// 当前页
const current = ref(1);
// 每页条数
const pageSize = ref(10);

// 待搜索的表单名称
const formTitle = ref("");
// 搜索执行情况
const loading = ref(false);

// 表单信息集合
const formDataGroup = ref([]);

// 表格列的配置描述
const columns = ref([
  {
    title: '序号',
    dataIndex: 'key',
    key: 'key',
  },
  {
    title: '表单名称',
    dataIndex: 'title',
    key: 'title',
  },
  {
    title: '发布时间',
    dataIndex: 'date',
    key: 'date',
  },
  {
    title: '收集答卷数量',
    dataIndex: 'answerNum',
    key: 'answerNum',
  },
  {
    title: '表单操作',
    dataIndex: 'operation',
  }
]);

// 选中表单相应的地址
const selectedUrl = ref("");

// 选中表单相应的 formDataJson
const selectedJson = ref({"title":"表单标题","content":[]});

// 二维码大小
const size = ref(150);

// 二维码显示情况
const qrcodeVisible = ref(false);

// 预览框的显示情况
const previewVisible = ref(false);

// 创建表单信息集合
function createFormDataGroup(data) {
  formDataGroup.value = data.map((val, index) => {
    const formInfo = JSON.parse(val);
    formInfo.key = ((current.value - 1) * pageSize.value + index + 1).toString();
    return formInfo;
  });
}

// 搜索
function search(value, event) {
  // 后端接入后使用
  // loading.value = true;
  axios.get("formShow.do", {
    params: {
      // 当前页
      PageIndex: 1,
      // 每页记录数
      PageSize: pageSize.value,
      // 其他的查询字段
      QueryParam: value  
    }, 
  }).then((result) => {
    current.value = 1;
    createFormDataGroup(result.data);
    loading.value = false;
  }).catch((error) => {
    message.error("搜索失败");
    console.log(error);
  });
}

// 页数改变 
function pageChange(page, pageSize) {
  // 后端接入后使用
  // loading.value = true;
  axios.get("formShow.do", {
    params: {
      // 当前页
      PageIndex: page,
      // 每页记录数
      PageSize: pageSize,
      // 其他的查询字段
      QueryParam: formTitle.value  
    }, 
  }).then((result) => {
    createFormDataGroup(result.data);
    loading.value = false;
  }).catch((error) => {
      console.log(error);
  });
}

// 页面跳转 => 打开新页面
function openLink(url) {
  window.open(url);
}

// 二维码生成
function qrcodeCreate(url) {
  selectedUrl.value = url;
  qrcodeVisible.value = true;  
}

// 预览表单
function previewShow(formData) {
  selectedJson.value = formData;
  previewVisible.value = true;
}

// 关闭表单预览
function previewClose() {
  previewVisible.value = false;
}

// 在组件挂载完成后执行
onMounted(() => {
  // 后端接入后使用
  // loading.value = true;
  axios.get("formShow.do", {
    params: {
      // 当前页
      PageIndex: 1,
      // 每页记录数
      PageSize: pageSize.value,
      // 其他的查询字段
      QueryParam: ""  
    }, 
  }).then((result) => {
    current.value = 1;
    formDataTotal.value = result.data.formDataTotal;
    createFormDataGroup(result.data.formDataGroup);
    loading.value = false;
  }).catch((error) => {
    console.log(error);
  })
});
</script>

<style scoped>
.container {
  height: 100%;
}

.search-button {
  margin: 10px auto;
  width: 360px;
}

.pagination {
  margin: 10px auto;
}

.download-button {
  width: 100px;
}

@media (max-height: 480px) {
  /* 
  .container {
    display: none;
  } 
  */
}
</style>