<template>
  <a-layout class="container">
    <a-layout-sider theme="light" class="main-left">
    </a-layout-sider>
    
    <a-layout class="content">
      <a-form>
        <div class="form-title">
          {{ formData.title }}
        </div>

        <a-divider></a-divider>
        
        <template v-for="item in formData.content">
          <a-form-item class="item-content">
            <FormGenerator :element="item" />
          </a-form-item>
        </template>  
      
      </a-form>
      
      <a-button type="primary" class="submit-button" @click="submit"> 提交 </a-button>
      
    </a-layout>

    <a-layout-sider theme="light" class="main-right">
    </a-layout-sider>
  </a-layout>
</template>

<script setup>
import FormGenerator from "@/components/Form/FormGenerator.vue";
import { message } from "ant-design-vue";
import { onBeforeMount, ref } from "vue";

const props = defineProps(["formDataJson"]);

// 表单信息
const formData = ref({"title":"表单标题","content":[]});

// 提交表单
function submit() {
  message.error("预览页面无法提交", 2);
}

// 在组件被挂载调用
onBeforeMount(() => {
  formData.value = JSON.parse(props.formDataJson);
});

</script>

<style scoped>
.container {
  height: 100%;
}

.content {
  overflow-y: scroll;
}

.form-title {
  text-align: center;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  margin-top: 10px;
}

.item-content {
  margin-left: 10px;
  margin-right: 10px;
}

.submit-button {
  margin: 0 auto;
  width: 100px;
}

@media (max-width: 680px) {
  .main-left,
  .main-right {
    display: none;
  }
}
</style>