// 文本复制至剪贴板

import { message } from "ant-design-vue";

/**
 * 文本复制
 * @param {String} text 待复制的文本
 */
export const copyText = (text) => {
  navigator.clipboard.writeText(text)
    .then(() => {
      message.success("复制成功", 2);
    }).catch((error) => {
      message.success("复制失败", 2);
      console.log(error);
    })
}